*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: sans-serif;
  background-color: #fff;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background-color: hsl(27deg, 66%, 92%);
  font-family: "DM Sans", sans-serif;
}

.container {
  max-width: 95%;
  display: flex;
  flex-direction: column;
  width: 25em;
}

.my-balance {
  background-color: hsl(10deg, 79%, 65%);
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1em;
  border-radius: 1em;
  margin-bottom: 1em;
}
.my-balance__info {
  color: hsl(33deg, 100%, 98%);
}
.my-balance__info .balance {
  font-size: 18px;
  font-weight: bold;
}
.my-balance__image {
  transform: scale(0.85);
}

.most {
  background-color: hsl(186deg, 34%, 60%) !important;
}

.chart-single:hover .chart-single__block {
  background-color: hsl(10deg, 79%, 75%);
}

.chart-single:hover .chart-single__block.most {
  background-color: hsl(186deg, 34%, 75%) !important;
}

.main-card {
  padding: 1.25em;
  background-color: hsl(33deg, 100%, 98%);
  border-radius: 1em;
}
.main-card h3 {
  margin-bottom: 1em;
}
.main-card .chart {
  width: 100%;
  display: grid;
  gap: 0.35em;
  grid-template-columns: repeat(7, minmax(1em, 100vw));
}
.main-card .chart-single {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
}
.main-card .chart-single__amount {
  background-color: hsl(25deg, 47%, 15%);
  color: hsl(27deg, 66%, 92%);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  margin-bottom: 0.5em;
  opacity: 0;
  font-size: 14px;
  font-weight: bold;
  transition: opacity 70ms ease;
}
.main-card .chart-single__amount.active {
  opacity: 1;
}
.main-card .chart-single__block {
  --max-height: 7em;
  height: calc(var(--max-height) * var(--procent));
  width: 90%;
  background-color: hsl(10deg, 79%, 65%);
  border-radius: 0.25em;
}
.main-card .chart-single__day {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-card .chart-single:nth-child(1) {
  grid-column: 1/2;
}
.main-card .chart-single:nth-child(2) {
  grid-column: 2/3;
}
.main-card .chart-single:nth-child(3) {
  grid-column: 3/4;
}
.main-card .chart-single:nth-child(4) {
  grid-column: 4/5;
}
.main-card .chart-single:nth-child(5) {
  grid-column: 5/6;
}
.main-card .chart-single:nth-child(6) {
  grid-column: 6/7;
}
.main-card hr {
  margin: 0.55em 0;
  background-color: hsl(27deg, 66%, 92%);
  border: none;
  height: 1px;
}
.main-card__footer {
  margin-top: 1.25em;
  display: flex;
  justify-content: space-between;
}
.main-card__footer-this {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main-card__footer-this p {
  font-size: 12px;
  color: hsl(28deg, 10%, 53%);
}
.main-card__footer-this span {
  font-size: 32px;
  font-weight: bold;
}
.main-card__footer-previous {
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main-card__footer-previous span {
  font-weight: bold;
}
.main-card__footer-previous p {
  font-size: 14px;
  color: hsl(28deg, 10%, 53%);
}